body {
  margin: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-family: 'Exo', sans-serif;
  font-size: 15px;
  line-height: 1;
  font-weight: 300;
}

.align-center {
  text-align: center;
}

/* Header */

.header {
  height: 100px;
  background-image: url("/images/background.jpg");
  background-size: cover;
  background-position: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.tittle-lg {
  margin: 0;
  font-size: 28px;
  color: white;
  @media (min-width: 418px) {
  font-size: 36px;
  }
}

/* Input */

.inputByName {
  height: 30px;
  border: none;
  border-bottom: solid 1px #bf1e2e;
  font-size: 14px;
  width: 80%;
  max-width: 300px;
  text-align: center;
  padding-top: 40px;
  margin-bottom: 40px;
}

input:focus{
    outline: none;
}

/* Card */

ul {
  list-style-type: none;
  padding-left: 0;
}

.card {
  border-radius: 10px;
  height: 180px;
  width: 55%;
  border-radius: 10px;
  background-color: #bf1e2e;
  padding-bottom: 20px;
  padding-bottom: 35px;
}

.container-image {
  text-align: center;
  background-color: white;
  border: 1px solid #bf1e2e;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.text-card {
  color: white;
  padding: 10px 0 0 15px;
}

.id {
  font-size: 12px;
}

.tittle-pokemon {
  font-size: 18px;
}

.tittle-pokemon:first-letter {
    text-transform: uppercase;
}


.wrapper-grid {
  display: grid;
  grid-row-gap: 40px;
  -ms-flex-pack: center;
      justify-content: center;
  justify-items: center;
  grid-template-rows: auto;
  grid-template-columns: 100%;
}

@media screen and (min-width: 768px) {
  .wrapper-grid {
    grid-column-gap: 40px;
    grid-template-columns: 40% 40%;
  }
}

@media screen and (min-width: 1024) {
  .wrapper-grid {
    grid-template-columns: 25% 25% 25%;
  }
}



